import React, {useState} from 'react'
import styled from 'styled-components'

const StyledNavbar = styled.div`
@keyframes slide {
    0% {
      transform: translateY(-100%);
    }
    100% {
      transform: translateY(0);
    }
}

color: #FFB7CB;
background-color: black;
width: 100%;
display: flex;
position: sticky;
left: 0;
top: 0;
z-index: 2;
height: 70px;
animation: 1s ease-out 0s slide;
`
const NavbarLeft = styled.div`
width: 30%;
display: flex;
align-items: center;
padding: 5px;

h4 {
    font-family: 'Questrial', sans-serif;
    font-weight: 600;
    font-size: 1.2rem;
}

@media (max-width: 616px) { 
    h4 {
    font-size: 1rem;
    }
}
`

const NavbarRight = styled.div`
display: flex;
align-items: center;
justify-content: flex-end;
width: 70%;

}
`

const NavMenu = styled.div`
display: flex;

@media (max-width: 700px) {
position: absolute;
top: 70px;
left: 0;
width: 100%;
align-items: center;
justify-content: center;
background-color: black;
display: ${({isNavbar}) => isNavbar ? 'flex' : 'none'}
}
`

const Navlink = styled.div`
padding: 5px;
flex-wrap: no-wrap;
font-family: 'Questrial', sans-serif;
font-size: 1.1rem;
display: flex;
align-items: center;

&:hover {
    color: #D70170;
    cursor: pointer;
}

@media (max-width: 700px) { 
    justify-content: center;
    width: 100px;
    font-size: 1rem;
}
`
const MenuButton = styled.div`
display: none;
@media (max-width: 700px) {
    display: block;
  }
`
const StyledBurger = styled.div`
width: 20px;
height: 20px;
display: flex;
justify-content: space-around;
flex-flow: column nowrap;
z-index: 2;
cursor: pointer;
transform-origin: 1px;
transition: all 0.3s;
margin-right: 20px;
padding: 2px;

  div {
      width: 20px;
      height: 3px;
      background-color: #FFB7CB;
      border-radius: 10px;
      transform-origin: 1px;
      transition: all 0.3s;
    &:nth-child(1) {
        transform: ${({open}) => open ? 'rotate(45deg)' : 'rotate(0)'};
    }

    &:nth-child(2) {
      transform: ${({open}) => open ? 'translateX(100%)' : 'translateX(0)'};
      opacity: ${({open}) => open ? 0 : 1};
    }

    &:nth-child(3) {
      transform: ${({open}) => open ? 'rotate(-45deg)' : 'rotate(0)'};
  }
      }
      
  }


  `;
const Navbar = ({open, setOpen, isNavbar, setIsNavbar, setIsHome, setIsAbout, setIsProjects, setIsContact}) => {
    return (
        <StyledNavbar>  

            <NavbarLeft>
                <h4>W. Ratajczak</h4>
            </NavbarLeft>
            <NavbarRight> 
                <NavMenu isNavbar={isNavbar}>
                <Navlink onClick={() => {
                    setIsHome(true)
                    setIsAbout(false)
                    setIsProjects(false)
                    setIsContact(false)}}>
                        Home
                </Navlink>
                <Navlink onClick={() => {
                    setIsHome(false)
                    setIsAbout(true)
                    setIsProjects(false)
                    setIsContact(false)}}>
                        About me
                </Navlink>
                <Navlink onClick={() => {
                    setIsHome(false)
                    setIsAbout(false)
                    setIsProjects(true)
                    setIsContact(false)}}>Projects</Navlink>
                <Navlink onClick={() => {
                    setIsHome(false)
                    setIsAbout(false)
                    setIsProjects(false)
                    setIsContact(true)}}>Contact</Navlink>
                </NavMenu>
                <MenuButton>
                    <StyledBurger open={open} onClick={() => {
                        setOpen(!open)
                        setIsNavbar(!isNavbar)
                        }}>
                        <div />
                        <div />
                        <div />
                    </StyledBurger>
                </MenuButton>
            </NavbarRight>
        </StyledNavbar>
    )
}

export default Navbar
