import React from 'react'
import styled from 'styled-components'
import bg from './bg.jpg'
import facebook from './facebook.png'
import linkedin from './linkedin.png'
import github from './github.png'

const StyledHome = styled.div`
background-color: #3D2A35;
width: 100%;
`

const HomeContent = styled.div`

@keyframes slider {
    0% {
        background-position: -60px, -20px;
      }
      50% {
        background-position: -170px, -200px;
      }
      100% {
        background-position: -60px, -20px;
      }
}

width: 100%;
color: #FF958E;
background-image: url(${bg});
background-size: cover;
background-repeat: no-repeat;
animation: slider 10s linear infinite;
transition: slider 0.5s;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
height: calc(100vh - 70px);

`

const HomeTitle = styled.div`
font-weight: 600;
display: flex;
align-items: center;
text-align: center;
justify-content: center;
padding: 18% 0px 16% 0px;
width: 100%;
font-family: 'Staatliches', cursive;


h4 {
    margin: 0;
    padding: 0;
    font-size: 4rem;
}

@media (max-width: 700px) { 
    padding: 14% 0px 15% 0px;  
    h4 {
    font-size: 2.2rem;
    }
}
`

const Socials = styled.div`
display: flex;
`

const Social = styled.div`
padding: 10px 2px 10px 2px;

img {
    height: 40px;
    transition: width, height;

    &:hover {
        background-color: #D70170;
        border-radius: 50px;
    }
}

@media (max-width: 616px) { 
    img {
        height: 30px;
    }
}
`
const Home = () => {
    return (
        <StyledHome >
            <HomeContent>
                <HomeTitle>
                    <h4>Let's create something beautiful together!</h4>
                </HomeTitle>
                <Socials>
                    <Social>
                    <a href="https://www.linkedin.com/in/wojciech-ratajczak-1aa362150/">
                        <img src={linkedin}/>
                    </a>
                    </Social>
                    <Social>
                    <a href="https://www.facebook.com/wojciacho.jajczak/">
                    <img src={facebook}/>
                    </a>
                    </Social>
                    <Social>
                    <a href="https://github.com/wojtekrat">
                    <img src={github}/>
                    </a>
                    </Social>
                </Socials>
            </HomeContent>
        </StyledHome>
    )
}

export default Home
