import React from 'react'
import styled from 'styled-components'
import wojtek from './wojtek.jpg'

const StyledAbout = styled.div`
height: fit-content;
display: flex;
flex-direction: column;
align-items: center;
background-color: #3D2A35;
padding-top: 5%;

@media (max-width: 700px) {
    padding-top: 1%;
}
`
const AboutContent = styled.div`
display: flex;
height: calc(100vh - 70px);

@media (max-width: 700px) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

`
const AboutTitle = styled.div`
color: #D70170;
font-size: 1.5rem;
font-family: 'Staatliches', cursive;
`
const AboutInfo = styled.div`
background-color: #3D2A35;
color: #FFB7CB;
width: 60%;
text-align: left;
@media (max-width: 700px) {
    width: 90%;
}
`

const AboutText = styled.div`
font-size: 1.1rem;

media (max-width: 700px) {
    font-size: 1rem;
}
`

const AboutSkills = styled.div`

`

const AboutImage = styled.div`
display: flex;
justify-content: flex-end;
margin-right: 2%;
width: 30%;
    img {
        max-height: 280px;
        border: 1px solid #D70170;
        border-radius: 10px;
    }
    @media (max-width: 1024px) {
        img {
            max-height: 230px;
        }
    }
    @media (max-width: 700px) {
        width: 90%;
        justify-content: center;
     img {
         max-height: 200px;
         margin-bottom: 10%;
     }   
    }
`
const About = () => {
    return (
        <StyledAbout>
            
            <AboutContent>
                <AboutImage>
                    <img src={wojtek}/>
                </AboutImage>
                <AboutInfo>
                <AboutTitle>About me</AboutTitle>
                    <AboutText><p>Hi, my name is Wojtek Ratajczak and I am 29 years old self taught web developer based in Wroclaw, PL.
                        </p><p>Last 2 years I have spent learning HTML, CSS, JavaScript, React.js, creating websites and apps and also I have acquired some commercial experience.<br></br> All of my recent projects are available in my Github repositories, so you can check if my code is clear and efficient. </p><p>Now I am looking for opportunities to develop myself further ways and I would like to try something more complex and work as a front end developer.</p> 
                    </AboutText>
                    <AboutSkills>

                    </AboutSkills>
                </AboutInfo>
                
            </AboutContent>
        </StyledAbout>
    )
}

export default About
